import { Ref, watch } from "vue"
import { Grid } from "./graphics"

type GridParamGetters = [
	() => Ref<number>["value"],
	() => Ref<number>["value"],
	() => Ref<number>["value"],
	() => Ref<number>["value"],
	() => Ref<number>["value"],
	() => Ref<number>["value"],
]

type GridWatchersConfig = {
	grid: Grid;
	gridActions: (() => void)[];
	minAirflow: Ref<number>;
	maxAirflow: Ref<number>;
	airflowInterval: Ref<number>;
	minPressureRatio: Ref<number>;
	maxPressureRatio: Ref<number>;
	pressureRatioStep: Ref<number>;
}

export const useGridWatchers = (config: GridWatchersConfig) => {
	const {
		grid,
		gridActions,
		minAirflow,
		maxAirflow,
		airflowInterval,
		minPressureRatio,
		maxPressureRatio,
		pressureRatioStep
	} = config;

	watch<GridParamGetters>(
		[
			() => minAirflow.value,
			() => maxAirflow.value,
			() => airflowInterval.value,
			() => minPressureRatio.value,
			() => maxPressureRatio.value,
			() => pressureRatioStep.value,
		],
		(newValues) => {
			const [
				minX,
				maxX,
				xInterval,
				minY,
				maxY,
				yInterval,
			] = newValues

			grid.setup({
				minX,
				maxX,
				xInterval,
				minY,
				maxY,
				yInterval
			})

			gridActions.forEach(a => a())
		}
	)
}