<template>
	<canvas
		:id="canvasId"
		:height="height"
		:width="width" />
</template>

<script lang="ts">
import { defineComponent, computed, onMounted } from "vue"
import { useStore } from "vuex";
import { buildAccessor as buildGridAccessor } from "@/store/grid";
import { Grid } from "./graphics";
import { useGridWatchers } from "./grid-watchers";

type GridProps = {
	width: number;
	height: number;
	canvasId: string;
	padding: number;
}

export default defineComponent({
	name: "Grid",
	props: {
		height: Number,
		width: Number,
		canvasId: String,
		padding: Number,
	},
	setup: (props) => {
		const { canvasId, padding, height, width } = props as GridProps;
		const store = useStore();

		const minAirflow = computed(buildGridAccessor("minAirflow", store));
		const maxAirflow = computed(buildGridAccessor("maxAirflow", store));
		const airflowInterval = computed(buildGridAccessor("airflowInterval", store));
		const minPressureRatio = computed(buildGridAccessor("minPressureRatio", store));
		const maxPressureRatio = computed(buildGridAccessor("maxPressureRatio", store));
		const pressureRatioStep = computed(buildGridAccessor("pressureRatioStep", store));

		onMounted(() => {
			const gridCanvas = document.getElementById(canvasId) as HTMLCanvasElement;

			const grid = new Grid({
				canvasRef: gridCanvas,
				height,
				width,
				padding,
				minX: minAirflow.value,
				maxX: maxAirflow.value,
				minY: minPressureRatio.value,
				maxY: maxPressureRatio.value,
				xInterval: airflowInterval.value,
				yInterval: pressureRatioStep.value
			});

			grid.drawGrid();
			grid.drawAxis();

			useGridWatchers({
				grid,
				gridActions: [grid.drawGrid.bind(grid), grid.drawAxis.bind(grid)],
				minAirflow,
				maxAirflow,
				airflowInterval,
				minPressureRatio,
				maxPressureRatio,
				pressureRatioStep
			})
		});
	},
})
</script>
