// const GAS_CONSTANT = 8.314463; // J⋅K−1⋅mol−1
const AIR_SPECIFIC_GAS_CONSTANT = 287.058; // J/(kg·K)
const ZERO_CELSIUS_KELVIN = 273.15;
const LBS_PER_KG = 2.204623;

type CubicMeters = number
type Pascals = number
type Celcius = number
type KgM3 = number
type KgS = number

type IGetAirDensity = (p: { temperature: Celcius; pressure: Pascals }) => KgM3
export const getAirDensity: IGetAirDensity = ({ temperature, pressure }) => (
	pressure / ((ZERO_CELSIUS_KELVIN + (temperature)) * AIR_SPECIFIC_GAS_CONSTANT)
);

type IGetVolume = (p: {
	displacement: CubicMeters;
	volumetricEfficiency?: number;
	throttling?: number;
}) => CubicMeters
export const getVolume: IGetVolume = ({
	displacement,
	volumetricEfficiency = 1,
	throttling = 1,
}) => (displacement * volumetricEfficiency * throttling);

type IGetVolumeFlow = <T extends number>(p: { rpm: number; volume: T }) => number
export const getVolumeFlow: IGetVolumeFlow = ({ rpm, volume }) => (
	(volume * rpm) / (2 * 60)
);

type IGetMAF = (p: {
	rpm: number;
	displacement: CubicMeters;
	temperature: Celcius;
	pressure: Pascals;
}, converter?: (i: number) => number) => KgS
export const getMAF: IGetMAF = ({
	rpm,
	displacement,
	temperature,
	pressure,
}, converter = i => i) => {
	const volume = getVolume({ displacement });
	return converter(getVolumeFlow({ rpm, volume }) * getAirDensity({ temperature, pressure }));
};

type IGetRPMfromMAF = (p: {
	maf: number;
	displacement: number;
	temperature: number;
	pressure: number;
}) => number
export const getRPMfromMAF: IGetRPMfromMAF = ({
	maf, displacement, temperature, pressure
}) => {
	const airDensity = getAirDensity({ temperature, pressure })
	const volumeFlow = maf / airDensity;
	const rpm = volumeFlow * 60 * 2 / displacement;
	return rpm
}

export const convertToLbsMin = (input: number): number => input * LBS_PER_KG * 60;
export const convertToKgS = (input: number): number => (input / LBS_PER_KG) / 60;

export enum AirflowUnits {
	kgs = "kg/s",
	lbsmin = "lbs/min"
}