import { createStore } from "vuex";
import { ExternalMapConfigState, externalMap, NAMESPACE as externalMapNamespace } from "./external-map";
import { GridConfigState, grid, NAMESPACE as gridNamespace } from "./grid";
import { EngineMapState, engineMap, NAMESPACE as engineMapNamespace } from "./engine-map"

export type State = {
	[externalMapNamespace]: ExternalMapConfigState;
	[gridNamespace]: GridConfigState;
	[engineMapNamespace]: EngineMapState;
}

export default createStore<State>({
	modules: {
		[externalMapNamespace]: externalMap,
		[gridNamespace]: grid,
		[engineMapNamespace]: engineMap,
	},
});
