
import { defineComponent, computed, onMounted, watch } from "vue"
import { useStore } from "vuex";
import { buildAccessor as buildExternalMapAccessor } from "@/store/external-map";
import { getImageRedrawer } from "./graphics";

type CompressorMapProps = {
	width: number;
	height: number;
	canvasId: string;
}

export default defineComponent({
	name: "CompressorMap",
	props: {
		height: Number,
		width: Number,
		canvasId: String,
	},
	setup: (props) => {
		const { canvasId } = props as CompressorMapProps
		const store = useStore();

		const hOffset = computed(buildExternalMapAccessor("hOffset", store))
		const vOffset = computed(buildExternalMapAccessor("vOffset", store))
		const hScale = computed(buildExternalMapAccessor("hScale", store))
		const vScale = computed(buildExternalMapAccessor("vScale", store))

		onMounted(() => {
			const imgCanvas = document.getElementById(canvasId) as HTMLCanvasElement;

			const img = new Image();
			img.src = "./example_compressor_map.jpg";

			img.onload = () => {
				imgCanvas // eslint-disable-line
					.getContext("2d")!
					.drawImage(
						img,
						hOffset.value,
						vOffset.value,
						Math.floor(hScale.value * img.width),
						Math.floor(vScale.value * img.height),
					);
			};

			const redrawImage = getImageRedrawer({ canvas: imgCanvas, image: img });

			watch(
				[
					() => hOffset.value,
					() => vOffset.value,
					() => hScale.value,
					() => vScale.value
				],
				([
					hOffset,
					vOffset,
					hScale,
					vScale
				]) => {
					redrawImage({
						hOffset,
						vOffset,
						hScale,
						vScale,
					})
				},
			);
		});
	},
})
