const GRID_STROKE_STYLE = "rgb(134,136,138)";

export const greatestCommonDivisor = (a: number, b: number): number => (b === 0
	? a
	: (greatestCommonDivisor(b, a % b)));

type GridConstructor = {
	canvasRef: HTMLCanvasElement;
	height: number;
	width: number;
	padding: number;
	minX: number;
	minY: number;
	maxX: number;
	maxY: number;
	xInterval?: number;
	yInterval?: number;
}

export class Grid {
	private context: CanvasRenderingContext2D;

	private height: number;

	private width: number;

	private padding: number;

	private minX: number;

	private minY: number;

	private maxX: number;

	private maxY: number;

	private gridWidth: number;

	private gridHeight: number;

	private origin: [number, number];

	private xInterval: number;
	private yInterval: number;

	private xAxisSpacing: number;
	private yAxisSpacing: number;

	constructor(config: GridConstructor) {
		const {
			canvasRef,
			height,
			width,
			padding,
			minX,
			minY,
			maxX,
			maxY,
			yInterval = 0,
			xInterval = 0,
		} = config;

		this.context = canvasRef.getContext("2d")!; // eslint-disable-line
		this.height = height;
		this.width = width;
		this.padding = padding;
		this.minX = minX;
		this.minY = minY;
		this.maxX = maxX;
		this.maxY = maxY;

		this.gridWidth = width - 2 * padding;
		this.gridHeight = height - 2 * padding;

		this.origin = [padding, height - padding];
		this.context.font = "10px sans-serif";

		this.xInterval = xInterval === 0 ? (maxX - minX) : xInterval;
		this.yInterval = yInterval === 0 ? (maxY - minY) : yInterval;

		this.xAxisSpacing = Math.floor(this.gridWidth * this.xInterval / (maxX - minX));
		this.yAxisSpacing = Math.floor(this.gridHeight * this.yInterval / (maxY - minY));
	}

	setup(config: Partial<GridConstructor>) {
		this.clear()

		const mergedConfig = {
			...this,
			...config
		}

		const {
			height,
			width,
			padding,
			minX,
			minY,
			maxX,
			maxY,
			yInterval = 0,
			xInterval = 0,
		} = mergedConfig;

		this.height = height;
		this.width = width;
		this.padding = padding;
		this.minX = minX;
		this.minY = minY;
		this.maxX = maxX;
		this.maxY = maxY;

		this.gridWidth = width - 2 * padding;
		this.gridHeight = height - 2 * padding;

		this.origin = [padding, height - padding];
		this.context.font = "10px sans-serif";

		this.xInterval = xInterval === 0 ? (maxX - minX) : xInterval;
		this.yInterval = yInterval === 0 ? (maxY - minY) : yInterval;

		this.xAxisSpacing = Math.floor(this.gridWidth * this.xInterval / (maxX - minX));
		this.yAxisSpacing = Math.floor(this.gridHeight * this.yInterval / (maxY - minY));
	}

	clear() {
		this.context.clearRect(0, 0, this.width, this.height);
	}

	drawGrid() {
		const [originX, originY] = this.origin;
		const {
			context,
			gridWidth,
			gridHeight,
			xAxisSpacing,
			yAxisSpacing,
			padding,
		} = this;
		context.beginPath();
		context.strokeStyle = GRID_STROKE_STYLE;
		context.moveTo(originX, originY);

		for (let i = originX; i <= (originX + gridWidth); i += xAxisSpacing) {
			context.moveTo(i, originY);
			context.lineTo(i, originY - gridHeight);
		}

		for (let j = originY; j >= padding; j -= yAxisSpacing) {
			context.moveTo(originX, j);
			context.lineTo(originX + gridWidth, j);
		}

		context.stroke();
	}

	drawAxis() {
		const {
			context,
			gridWidth,
			padding,
			xAxisSpacing,
			yAxisSpacing,
			xInterval,
			yInterval,
			minX,
			maxY,
			minY,
		} = this;

		context.beginPath();

		const [originX, originY] = this.origin;

		for (
			let x = originX, value = minX;
			x <= (originX + gridWidth);
			x += xAxisSpacing, value += xInterval
		) {
			const label = value.toFixed(3);
			context.fillText(label, x - context.measureText(label).width / 2, originY + 14);
		}

		for (
			let y = originY, value = minY;
			y >= padding;
			y -= yAxisSpacing, value += yInterval
		) {
			const label = value.toFixed(2);
			context.fillText(label, originX - context.measureText(label).width - 4, y + 5);
		}

		context.beginPath();
		context.rect(
			this.transformX(minX),
			this.transformY(maxY),
			this.gridWidth,
			this.gridHeight,
		);
		context.stroke();
	}

	transformX(input: number) {
		const scale = this.gridWidth / (this.maxX - this.minX);

		return Math.floor((input - this.minX) * scale) + this.padding;
	}

	transformY(input: number) {
		const scale = this.gridHeight / (this.maxY - this.minY);

		return Math.floor(this.gridHeight - (input - this.minY) * scale) + this.padding;
	}

	moveTo(x: number, y: number) {
		return this.context.moveTo(
			this.transformX(x),
			this.transformY(y),
		);
	}

	lineTo(x: number, y: number) {
		return this.context.lineTo(
			this.transformX(x),
			this.transformY(y),
		);
	}

	stroke() {
		return this.context.stroke();
	}

	drawCircle(x: number, y: number, radius: number, fill = false) {
		this.context.beginPath()
		this.context.moveTo(this.transformX(x) + radius, this.transformY(y))
		this.context.arc(this.transformX(x), this.transformY(y), radius, 0, Math.PI * 2)
		if (fill) {
			this.context.fill()
		}
		this.context.stroke()
	}

	drawMap(points: { x: number; y: number }[]) {
		this.clear();
		this.context.beginPath();

		this.context.strokeStyle = "green";
		this.context.lineWidth = 3;

		const [first, ...restPoints] = points;
		this.moveTo(first.x, first.y);

		restPoints.forEach(({ x, y }) => {
			this.lineTo(x, y);
			this.moveTo(x, y);
		});

		this.lineTo(first.x, first.y);
		this.context.closePath();
		this.stroke();

		this.context.beginPath();
		this.context.strokeStyle = "red";
		this.context.fillStyle = "pink";
		this.context.lineWidth = 1;

		points.forEach(({ x, y }) => {
			this.drawCircle(x, y, 5, true)
		})
	}
}

export const getImageRedrawer = (
	{ image, canvas }: { image: CanvasImageSource; canvas: HTMLCanvasElement },
) => (
	config: {
		hOffset: number;
		vOffset: number;
		hScale: number;
		vScale: number;
	},
) => {
	const {
		width: canvasWidth,
		height: canvasHeight,
	} = canvas;

	const ctx = canvas.getContext("2d")!; // eslint-disable-line

	const {
		hOffset,
		vOffset,
		hScale,
		vScale,
	} = config;

	ctx.clearRect(0, 0, canvasWidth, canvasHeight);
	ctx.drawImage(
		image,
		hOffset,
		vOffset,
		Math.floor(hScale * (image.width as number)),
		Math.floor(vScale * (image.height as number)),
	);
};
