<template>
	<canvas
		:id="canvasId"
		:height="height"
		:width="width"
		@mousemove="handleHover"
		@mousedown="handleMouseDown"
		@mouseup="handleMouseUp" />
	<div class="tooltip">
		PressureRatio: {{ (Math.round(pointerData.pressureRatio * 100) / 100).toFixed(2) }}<br />
		Airflow: {{ (Math.round(pointerData.massAirflow * 1000) / 1000).toFixed(3) }} {{ airflowUnits }}<br />
		rpm: {{ pointerData.rpm }}
	</div>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted, watch, ref, onBeforeUnmount } from "vue"
import { useStore } from "vuex";
import {
	buildAccessor as buildGridAccessor,
	buildAirflowUnitsAccessor,
} from "@/store/grid";
import { Grid } from "./graphics";
import {
	buildMapPointsGetter,
	buildAccessor as buildEngineMapAccessor,
	buildMapPointModifier,
} from "@/store/engine-map";
import { getMAF as getMAFSI, AirflowUnits, convertToLbsMin, getRPMfromMAF, convertToKgS } from "./physics";
import { useGridWatchers } from "./grid-watchers";

type EngineMapProps = {
	width: number;
	height: number;
	canvasId: string;
	padding: number;
}

type IGetMaf = (p: {
	displacementCC: number;
	temperature: number;
	pressureBar: number;
	rpm: number;
}) => number
const getMAFKgS: IGetMaf = (payload) => getMAFSI({
	...payload,
	displacement: payload.displacementCC / 1000000,
	pressure: payload.pressureBar * 100000
})
const getMAFLBSMIN: IGetMaf = (payload) => convertToLbsMin(getMAFKgS(payload))

export default defineComponent({
	name: "Map",
	props: {
		height: Number,
		width: Number,
		canvasId: String,
		padding: Number,
	},
	setup: (props) => {
		const { canvasId, height, width, padding } = props as EngineMapProps;
		const mapWidth = width - 2 * padding
		const mapHeight = height - 2 * padding

		let graphCanvas: HTMLCanvasElement;
		let tooltip: HTMLElement;
		const graphCanvasViewport = ref<DOMRect>({} as DOMRect);
		const windowResizeHandler = () => { graphCanvasViewport.value = graphCanvas?.getBoundingClientRect() }

		const store = useStore();

		const minAirflow = computed(buildGridAccessor("minAirflow", store));
		const maxAirflow = computed(buildGridAccessor("maxAirflow", store));
		const airflowInterval = computed(buildGridAccessor("airflowInterval", store));
		const minPressureRatio = computed(buildGridAccessor("minPressureRatio", store));
		const maxPressureRatio = computed(buildGridAccessor("maxPressureRatio", store));
		const pressureRatioStep = computed(buildGridAccessor("pressureRatioStep", store));
		const airflowUnits = computed(buildAirflowUnitsAccessor(store));

		const engineDisplacement = computed(buildEngineMapAccessor("displacement", store));
		const ambientAirPressure = computed(buildEngineMapAccessor("ambientAirPressure", store));
		const ambientAirTemperature = computed(buildEngineMapAccessor("ambientAirTemperature", store));
		const pointerData = computed(buildEngineMapAccessor("pointerData", store));

		const setMapPoint = buildMapPointModifier(store);

		const engineMapRawPoints = computed(buildMapPointsGetter(store));
		const engineMapPoints = computed(() => {
			const getMAF = airflowUnits.value === AirflowUnits.kgs
				? getMAFKgS
				: getMAFLBSMIN

			return [
				{
					x: getMAF({
						displacementCC: engineDisplacement.value,
						temperature: ambientAirTemperature.value,
						pressureBar: minPressureRatio.value,
						rpm: engineMapRawPoints.value[0]?.rpm
					}),
					y: minPressureRatio.value
				},
				...engineMapRawPoints.value.map(p => ({
					x: getMAF({
						displacementCC: engineDisplacement.value,
						temperature: ambientAirTemperature.value,
						pressureBar: p.pressure,
						rpm: p.rpm
					}),
					y: p.pressure / (ambientAirPressure.value / 1000)
				})),
				{
					x: getMAF({
						displacementCC: engineDisplacement.value,
						temperature: ambientAirTemperature.value,
						pressureBar: minPressureRatio.value,
						rpm: engineMapRawPoints.value[engineMapRawPoints.value.length - 1]?.rpm
					}),
					y: minPressureRatio.value
				}
			]
		})

		onMounted(() => {
			graphCanvas = document.getElementById(canvasId) as HTMLCanvasElement;
			tooltip = document.querySelector(".tooltip") as HTMLElement;
			graphCanvasViewport.value = graphCanvas.getBoundingClientRect()
			window.addEventListener("resize", windowResizeHandler);
			window.addEventListener("scroll", windowResizeHandler);

			const graph = new Grid({
				canvasRef: graphCanvas,
				height,
				width,
				padding,
				minX: minAirflow.value,
				maxX: maxAirflow.value,
				minY: minPressureRatio.value,
				maxY: maxPressureRatio.value
			});

			graph.drawMap(engineMapPoints.value)

			useGridWatchers({
				grid: graph,
				gridActions: [() => { graph.drawMap(engineMapPoints.value) }],
				minAirflow,
				maxAirflow,
				airflowInterval,
				minPressureRatio,
				maxPressureRatio,
				pressureRatioStep
			})

			watch(
				() => engineMapPoints.value,
				points => { graph.drawMap(points) }
			)
		});

		onBeforeUnmount(() => {
			window.removeEventListener("resize", windowResizeHandler);
			window.removeEventListener("scroll", windowResizeHandler);
		})

		let contextPoint: number | undefined

		const getMapDataFromAtCanvasXY = ({ x, y }: { x: number; y: number }): [number, number] | [] => {
			const { top, left } = graphCanvasViewport.value
			const mapY = y - top - padding
			const mapX = x - left - padding

			if (mapY < 0 || mapY >= mapHeight) { return [] }
			if (mapX < 0 || mapX >= mapWidth) { return [] }

		 	return [
				((1 - (mapY / mapHeight)) * (maxPressureRatio.value - minPressureRatio.value)) + minPressureRatio.value,
				((mapX / mapWidth) * (maxAirflow.value - minAirflow.value)) + minAirflow.value
			]
		}

		const getRPM = ({ maf, pressureRatio }: { maf: number; pressureRatio: number }): number => Math.round(getRPMfromMAF({
			maf: airflowUnits.value === AirflowUnits.kgs ? maf : convertToKgS(maf),
			displacement: engineDisplacement.value / 1000000,
			temperature: ambientAirTemperature.value,
			pressure: (ambientAirPressure.value / 1000) * 100000 * pressureRatio
		}))

		const handleHover = (e: MouseEvent) => {

			const [ pressureRatio, massAirflow ] = getMapDataFromAtCanvasXY(e);

			if (!pressureRatio || !massAirflow) {
				tooltip.style.display = "none"
				return
			}

			if (tooltip.style.display === "none") {
				tooltip.style.display = "block"
			}

			const { top, left } = graphCanvasViewport.value;
			tooltip.style.left = e.pageX - left  + "px"
			tooltip.style.top = e.pageY - top + "px"

			pointerData.value = {
				pressureRatio,
				massAirflow,
				rpm: getRPM({ maf: massAirflow, pressureRatio })
			}

			const rpm = getRPM({ maf: massAirflow, pressureRatio })

			if (contextPoint !== undefined) {
				setMapPoint({ rpm, pressure: (ambientAirPressure.value / 1000) * pressureRatio }, contextPoint)
			}
		}

		const handleMouseDown = (e: MouseEvent) => {
			const [ pressureRatio, massAirflow ] = getMapDataFromAtCanvasXY(e);

			if (!pressureRatio || !massAirflow) { return }

			const rpm = getRPM({ maf: massAirflow, pressureRatio })

			contextPoint = engineMapRawPoints.value.findIndex((point) => {
				if (point.rpm === rpm || (point.rpm * 1.05 > rpm && point.rpm * 0.95 < rpm)) {
					const pressureAtMouse = pressureRatio * ambientAirPressure.value / 1000

					if (
						point.pressure === pressureAtMouse ||
						(point.pressure * 1.01 > pressureAtMouse && point.pressure * 0.99 < pressureAtMouse)
					) {
						return true;
					}
				}

				return false;
			})
		}

		const handleMouseUp = () => { contextPoint = undefined }


		return {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			pointerData,
			airflowUnits
		}
	},
})
</script>

<style lang="scss" scoped>
.tooltip {
	display: none;
	background: #C8C8C7;
	margin-left: 30px;
	padding: 10px;
	position: absolute;
	z-index: 1000;
	width: 200px;
	height: 100px;
}
</style>
