import { Module, Store } from "vuex";
import { AirflowUnits, convertToLbsMin, convertToKgS } from "@/views/physics";

export const NAMESPACE = "grid"

export interface GridConfigState {
	airflowUnits: AirflowUnits;
	minAirflow: number;
	maxAirflow: number;
	airflowInterval: number;
	minPressureRatio: number;
	maxPressureRatio: number;
	pressureRatioStep: number;
}

const namespaced = (input: keyof GridConfigState): string => `${NAMESPACE}/${input}`;

export const buildAccessor = <T extends keyof GridConfigState>(
	property: T,
	store: Store<{}>
): {
	get: () => GridConfigState[T];
	set: (input: GridConfigState[T]) => void;
} => ({
		get: () => store.getters[namespaced(property)],
		set: (input) => store.commit(namespaced(property), input)
	})

export const buildAirflowUnitsAccessor = (store: Store<{}>): {
	get: () => AirflowUnits;
	set: (airflowUnits: AirflowUnits) => void;
} => ({
	get: () => store.getters[namespaced("airflowUnits")],
	set: (airflowUnits) => store.dispatch(namespaced("airflowUnits"), airflowUnits)
})

export const grid: Module<GridConfigState, {}> = {
	namespaced: true,
	state: {
		airflowUnits: AirflowUnits.lbsmin,
		minAirflow: 0,
		maxAirflow: 60,
		airflowInterval: 5,
		minPressureRatio: 1,
		maxPressureRatio: 4,
		pressureRatioStep: 0.25,
	},
	getters: {
		airflowUnits: ({ airflowUnits }) => airflowUnits,
		minAirflow: ({ minAirflow }) => minAirflow,
		maxAirflow: ({ maxAirflow }) => maxAirflow,
		airflowInterval: ({ airflowInterval }) => airflowInterval,
		minPressureRatio: ({ minPressureRatio }) => minPressureRatio,
		maxPressureRatio: ({ maxPressureRatio }) => maxPressureRatio,
		pressureRatioStep: ({ pressureRatioStep }) => pressureRatioStep,
	},
	mutations: {
		airflowUnits: (state, airflowUnits) => { state.airflowUnits = airflowUnits },
		minAirflow: (state, minAirflow) => { state.minAirflow = minAirflow },
		maxAirflow: (state, maxAirflow) => { state.maxAirflow = maxAirflow },
		airflowInterval: (state, airflowInterval) => { state.airflowInterval = airflowInterval },
		minPressureRatio: (state, minPressureRatio) => { state.minPressureRatio = minPressureRatio },
		maxPressureRatio: (state, maxPressureRatio) => { state.maxPressureRatio = maxPressureRatio },
		pressureRatioStep: (state, pressureRatioStep) => { state.pressureRatioStep = pressureRatioStep },
	},
	actions: {
		airflowUnits: ({ commit, state }, nextUnits: AirflowUnits) => {
			const {
				minAirflow,
				maxAirflow,
				airflowInterval,
				airflowUnits
			} = state;

			commit("airflowUnits", nextUnits)

			if (airflowUnits === AirflowUnits.kgs && nextUnits === AirflowUnits.lbsmin) {
				commit("minAirflow", Math.round(convertToLbsMin(minAirflow)))
				commit("maxAirflow", Math.round(convertToLbsMin(maxAirflow)))
				commit("airflowInterval", Math.round(convertToLbsMin(airflowInterval)))
			}

			if (airflowUnits === AirflowUnits.lbsmin && nextUnits === AirflowUnits.kgs) {
				commit("minAirflow", Math.round(convertToKgS(minAirflow) * 1000) / 1000)
				commit("maxAirflow", Math.round(convertToKgS(maxAirflow) * 1000) / 1000)
				commit("airflowInterval", Math.round(convertToKgS(airflowInterval) * 500) / 500)
			}
		}
	}
}
