
import { defineComponent } from "vue"
import CompressorMap from "./CompressorMap.vue"
import Grid from "./Grid.vue"
import EngineMap from "./EngineMap.vue"

enum MapLayer {
	CompressorMap = "compressor-map",
	Grid = "grid",
	EngineMap = "engine-map"
}

const defaultConfig = {
	width: 1208,
	height: 908,
	padding: 100
};

export default defineComponent({
	name: "Map",
	components: {
		CompressorMap,
		Grid,
		EngineMap
	},
	setup: () => ({
		MapLayer,
		canvasHeight: defaultConfig.height,
		canvasWidth: defaultConfig.width,
		gridPadding: defaultConfig.padding
	})
})
