import { Module, Store } from "vuex";

export const NAMESPACE = "externalMap"

export interface ExternalMapConfigState {
	hScale: number;
	hOffset: number;
	vScale: number;
	vOffset: number;
}

const namespaced = (input: keyof ExternalMapConfigState): string => `${NAMESPACE}/${input}`;

export const buildAccessor = <T extends keyof ExternalMapConfigState>(
	property: T,
	store: Store<{}>
): {
	get: () => ExternalMapConfigState[T];
	set: (input: ExternalMapConfigState[T]) => void;
} => ({
		get: () => store.getters[namespaced(property)],
		set: (input) => store.commit(namespaced(property), input)
	})

export const externalMap: Module<ExternalMapConfigState, {}> = {
	namespaced: true,
	state: {
		hScale: 0.687,
		hOffset: -52,
		vScale: 0.381,
		vOffset: 91,
	},
	getters: {
		hScale: ({ hScale }) => hScale,
		hOffset: ({ hOffset }) => hOffset,
		vScale: ({ vScale }) => vScale,
		vOffset: ({ vOffset }) => vOffset,
	},
	mutations: {
		hScale: (state, hScale) => { state.hScale = hScale },
		hOffset: (state, hOffset) => { state.hOffset = hOffset },
		vScale: (state, vScale) => { state.vScale = vScale },
		vOffset: (state, vOffset) => { state.vOffset = vOffset },
	}
}
