
import {
	defineComponent,
	computed,
} from "vue";
import { useStore } from "vuex";
import { buildAccessor as buildExternalMapAccessor } from "@/store/external-map";
import { buildAccessor as buildGridAccessor, buildAirflowUnitsAccessor } from "@/store/grid";
import { buildAccessor as buildEngineMapAccessor } from "@/store/engine-map";
import { AirflowUnits } from "./physics"
import Maps from "./Maps.vue"
import EngineMapPoints from "./EngineMapPoints.vue"

export default defineComponent({
	name: "Home",
	components: {
		Maps,
		EngineMapPoints
	},
	setup: () => {
		const store = useStore();

		const hOffset = computed(buildExternalMapAccessor("hOffset", store))
		const vOffset = computed(buildExternalMapAccessor("vOffset", store))
		const hScale = computed(buildExternalMapAccessor("hScale", store))
		const vScale = computed(buildExternalMapAccessor("vScale", store))

		const airflowUnits = computed(buildAirflowUnitsAccessor(store));
		const minAirflow = computed(buildGridAccessor("minAirflow", store));
		const maxAirflow = computed(buildGridAccessor("maxAirflow", store));
		const airflowInterval = computed(buildGridAccessor("airflowInterval", store));
		const minPressureRatio = computed(buildGridAccessor("minPressureRatio", store));
		const maxPressureRatio = computed(buildGridAccessor("maxPressureRatio", store));
		const pressureRatioStep = computed(buildGridAccessor("pressureRatioStep", store));

		const engineDisplacement = computed(buildEngineMapAccessor("displacement", store));
		const ambientAirPressure = computed(buildEngineMapAccessor("ambientAirPressure", store));
		const ambientAirTemperature = computed(buildEngineMapAccessor("ambientAirTemperature", store));
		const pointerData = computed(buildEngineMapAccessor("pointerData", store));

		const airflowUnitStep = computed(() => {
			if (airflowUnits.value === AirflowUnits.kgs) {
				return 0.05;
			}
			return 1;
		})

		return {
			AirflowUnits,
			hScale,
			vScale,
			hOffset,
			vOffset,
			airflowUnits,
			minAirflow,
			maxAirflow,
			airflowInterval,
			minPressureRatio,
			maxPressureRatio,
			pressureRatioStep,
			airflowUnitStep,
			engineDisplacement,
			ambientAirPressure,
			ambientAirTemperature,
			pointerData
		};
	},
});
