<template>
	<Maps />
	<div class="calculation-container">
		<form>
			<div class="row">
				<label for="airflow">Mass airflow units</label>
				<fieldset id="airflow">
					<select
						id="airflow-units"
						v-model="airflowUnits">
						<option :value="AirflowUnits.kgs">kg/s</option>
						<option :value="AirflowUnits.lbsmin">Lbs/min</option>
					</select>
					<div class="row">
						<label for="min-airflow">Minimum airflow</label>
						<input
							id="min-airflow"
							type="number"
							v-model="minAirflow"
							:step="airflowUnitStep">
					</div>
					<div class="row">
						<label for="max-airflow">Maximum airflow</label><input
							id="max-airflow"
							type="number"
							v-model="maxAirflow"
							:step="airflowUnitStep">
					</div>
					<div class="row">
						<label for="airflow-interval">Airflow grid step</label><input
							id="airflow-interval"
							type="number"
							v-model="airflowInterval"
							step="any">
					</div>
				</fieldset>
			</div>
			<div class="row">
				<fieldset>
					<div class="row">
						<label for="min-pressure-ratio">Min pressure ratio</label>
						<input id="min-pressure-ratio" type="number" v-model="minPressureRatio" step="0.5">
					</div>
					<div class="row">
						<label for="max-pressure-ratio">Max pressure ratio</label>
						<input id="max-pressure-ratio" type="number" v-model="maxPressureRatio" step="0.5">
					</div>
					<div class="row">
						<label for="max-pressure-ratio">Pressure ratio step</label>
						<input id="max-pressure-ratio" type="number" v-model="pressureRatioStep" step="any">
					</div>
				</fieldset>
			</div>
		</form>
		<form>
			<fieldset>
				<div class="row">
					<label for="h-scale">Horizontal scale</label>
					<input
						id="h-scale"
						type="number"
						step="0.001"
						v-model="hScale">
				</div>
				<div class="row">
					<label for="h-offset">Horizontal offset</label>
					<input
						id="h-offset"
						type="number"
						v-model="hOffset">
				</div>
				<div class="row">
					<label for="v-scale">Vertical scale</label>
					<input
						id="v-scale"
						type="number"
						step="0.001"
						v-model="vScale">
				</div>
				<div class="row">
					<label for="v-offset">Vertical offset</label>
					<input
						id="v-offset"
						type="number"
						v-model="vOffset">
				</div>
			</fieldset>
		</form>
		<form>
			<fieldset>
				<div class="row">
					<label for="engine-displacement">Engine displacement (cc)</label>
					<input id="engine-displacement" type="number" v-model="engineDisplacement">
				</div>
				<div class="row">
					<label for="ambient-air-temperature">Ambient air temperature (C)</label>
					<input id="ambient-air-temperature" type="number" v-model="ambientAirTemperature">
				</div>
				<div class="row">
					<label for="ambient-air-pressure">Ambient air absolute pressure (mbar)</label>
					<input id="ambient-air-pressure" type="number" v-model="ambientAirPressure">
				</div>
			</fieldset>
		</form>
		<EngineMapPoints />
		<div>
			pressureRatio: {{ (Math.round(pointerData.pressureRatio * 100) / 100).toFixed(2) }}<br />
			massAirflow: {{ (Math.round(pointerData.massAirflow * 1000) / 1000).toFixed(3) }}<br />
			rpm: {{ pointerData.rpm }}
		</div>
	</div>
</template>

<script lang="ts">
import {
	defineComponent,
	computed,
} from "vue";
import { useStore } from "vuex";
import { buildAccessor as buildExternalMapAccessor } from "@/store/external-map";
import { buildAccessor as buildGridAccessor, buildAirflowUnitsAccessor } from "@/store/grid";
import { buildAccessor as buildEngineMapAccessor } from "@/store/engine-map";
import { AirflowUnits } from "./physics"
import Maps from "./Maps.vue"
import EngineMapPoints from "./EngineMapPoints.vue"

export default defineComponent({
	name: "Home",
	components: {
		Maps,
		EngineMapPoints
	},
	setup: () => {
		const store = useStore();

		const hOffset = computed(buildExternalMapAccessor("hOffset", store))
		const vOffset = computed(buildExternalMapAccessor("vOffset", store))
		const hScale = computed(buildExternalMapAccessor("hScale", store))
		const vScale = computed(buildExternalMapAccessor("vScale", store))

		const airflowUnits = computed(buildAirflowUnitsAccessor(store));
		const minAirflow = computed(buildGridAccessor("minAirflow", store));
		const maxAirflow = computed(buildGridAccessor("maxAirflow", store));
		const airflowInterval = computed(buildGridAccessor("airflowInterval", store));
		const minPressureRatio = computed(buildGridAccessor("minPressureRatio", store));
		const maxPressureRatio = computed(buildGridAccessor("maxPressureRatio", store));
		const pressureRatioStep = computed(buildGridAccessor("pressureRatioStep", store));

		const engineDisplacement = computed(buildEngineMapAccessor("displacement", store));
		const ambientAirPressure = computed(buildEngineMapAccessor("ambientAirPressure", store));
		const ambientAirTemperature = computed(buildEngineMapAccessor("ambientAirTemperature", store));
		const pointerData = computed(buildEngineMapAccessor("pointerData", store));

		const airflowUnitStep = computed(() => {
			if (airflowUnits.value === AirflowUnits.kgs) {
				return 0.05;
			}
			return 1;
		})

		return {
			AirflowUnits,
			hScale,
			vScale,
			hOffset,
			vOffset,
			airflowUnits,
			minAirflow,
			maxAirflow,
			airflowInterval,
			minPressureRatio,
			maxPressureRatio,
			pressureRatioStep,
			airflowUnitStep,
			engineDisplacement,
			ambientAirPressure,
			ambientAirTemperature,
			pointerData
		};
	},
});
</script>

<style lang="scss">
.calculation-container {
	display: flex;
	flex-direction: column;
}
</style>
